import React from "react";
import { Check } from "lucide-react";

/**
 * Sidebar  -    
 * VERSION 2.0 -    5 
 * 
 * Props:
 * - activeSection:   
 * - scrollToSection:     
 * - progress:  (0-100%)
 */
export default function Sidebar({ activeSection, scrollToSection, progress = 0 }) {
  // 
  //    (5 )
  // 
  
  const sectionGroups = [
    {
      title: " ",
      sections: [
        { id: "hero", label: "" },
        { id: "glossary", label: "" },
        { id: "about-program", label: " " },
        { id: "roadmap", label: " " },
        { id: "checklist", label: "-" },
        { id: "org-steps", label: " " },
        { id: "prep-start-cc", label: "  Start-" },
      ],
    },
    {
      title: " ",
      sections: [
        { id: "start-cc", label: "Start-" },
        { id: "meetings-rhythm", label: " " },
        { id: "meeting-cycle", label: " " },
        { id: "roles", label: "" },
        { id: "wig-declaration", label: " WIG" },
        { id: "control-panel", label: " " },
      ],
    },
    {
      title: " ",
      sections: [
        { id: "tools-hub", label: "" },
        { id: "templates", label: "" },
        { id: "calendar", label: "" },
      ],
    },
    {
      title: " ",
      sections: [
        { id: "documents", label: "NDA" },
        { id: "rules", label: "" },
        { id: "ai-mentor", label: "AI-" },
      ],
    },
    {
      title: " ",
      sections: [
        { id: "final-cc", label: "Final-" },
      ],
    },
  ];

  return (
    <aside className="sidebar">
      <div className="sidebar-content">
        {/*   */}
        {sectionGroups.map((group, groupIdx) => (
          <div key={groupIdx} className="sidebar-group">
            <h3 className="sidebar-group-title">{group.title}</h3>
            <ul className="sidebar-list">
              {group.sections.map((section) => (
                <li key={section.id}>
                  <button
                    className={`sidebar-link ${
                      activeSection === section.id ? "active" : ""
                    }`}
                    onClick={() => scrollToSection(section.id)}
                  >
                    <span className="sidebar-link-icon">
                      {activeSection === section.id ? (
                        <Check size={16} />
                      ) : (
                        <span className="sidebar-dot" />
                      )}
                    </span>
                    <span>{section.label}</span>
                  </button>
                </li>
              ))}
            </ul>
          </div>
        ))}

        {/* - */}
        <div className="sidebar-progress">
          <div className="sidebar-progress-label"></div>
          <div className="sidebar-progress-bar">
            <div
              className="sidebar-progress-fill"
              style={{ width: `${progress}%` }}
            />
          </div>
          <div className="sidebar-progress-text">{progress}%</div>
        </div>
      </div>
    </aside>
  );
}