export const getLevelColor = (level) => {
  const colors = {
    nechto: "rgba(156, 163, 175, 0.8)", // 
    ultima: "rgba(99, 102, 241, 0.8)", // 
    group: "rgba(236, 72, 153, 0.8)", // 
  };
  return colors[level] || colors.nechto;
};

//   
export const getLevelLabel = (level) => {
  const labels = {
    nechto: "",
    ultima: "ULTIMA",
    group: "",
  };
  return labels[level] || level;
};
