import React from "react";
import { 
  Users, 
  UserCheck, 
  Clipboard, 
  Target, 
  Compass, 
  Crown, 
  MapPin, 
  Flag 
} from "lucide-react";

/**
 * Glossary  -    ULTIMA
 * VERSION 2.0 -   content.glossary  
 * 
 * Props:
 * - id: ID   
 * - content:  content  content.js
 */
export default function Glossary({ id = "glossary", content }) {
  const glossary = content?.glossary || {};
  const terms = glossary?.terms || [];

  //   (    React)
  const iconMap = {
    Users: Users,
    UserCheck: UserCheck,
    Clipboard: Clipboard,
    Target: Target,
    Compass: Compass,
    Crown: Crown,
    MapPin: MapPin,
    Flag: Flag,
  };

  //   
  const getIcon = (iconName) => {
    const IconComponent = iconMap[iconName];
    return IconComponent ? <IconComponent size={24} /> : null;
  };

  return (
    <section id={id} className="section">
      <div className="container">
        <div className="section-header">
          <h2 className="section-title">
            {glossary?.title || " "}
          </h2>
          <p className="section-subtitle">
            {glossary?.subtitle || " ,      "}
          </p>
        </div>

        <div className="glossary-grid">
          {terms.map((item, idx) => (
            <div key={idx} className="glossary-card">
              {/*  */}
              {item.icon && (
                <div className="glossary-card-icon">
                  {getIcon(item.icon)}
                </div>
              )}
              
              {/*  */}
              <h3 className="glossary-card-title">{item.term}</h3>
              
              {/*  */}
              <p className="glossary-card-description">{item.definition}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}