import React from "react";
import { Target, TrendingUp, BarChart2, Users, BookOpen, ArrowRight } from "lucide-react";

/**
 * FourDX - 4  
 *     ULTIMA
 */
export default function FourDX({ id = "4dx", content, scrollToSection }) {
  const fourDX = content?.fourDX || {};
  const title = fourDX?.title || "4  ";
  const subtitle = fourDX?.subtitle || "    ULTIMA";
  const intro = fourDX?.intro || "         The 4 Disciplines of Execution (FranklinCovey)";
  
  const disciplines = fourDX?.disciplines || [
    {
      number: 1,
      icon: "Target",
      title: "    ",
      subtitle: "Wildly Important Goals (WIG)",
      description: "  1-2  .            .",
      inUltima: " ULTIMA    WIG  6 ",
      formula: " X  Y  ",
      examples: [
        " 5 /  10 /  1 ",
        " 15   30  1 ",
        " 3%   8%  15 "
      ],
      link: "#wig-declaration"
    },
    {
      number: 2,
      icon: "TrendingUp",
      title: "    ",
      subtitle: "Lead Measures vs Lag Measures",
      description: "Lead        (, ). Lag    (, ). Lead   Lag.",
      inUltima: "    Lead-   ",
      formula: "Lead  Lag",
      examples: [
        "Lead: 50 /  Lag: 10   3   +2  ",
        "Lead: 3 /  Lag: +500   20   5 ",
        "Lead: 10 /  Lag: 2    "
      ],
      link: "#control-panel"
    },
    {
      number: 3,
      icon: "BarChart2",
      title: "  ",
      subtitle: "Scoreboard",
      description: "    .     WIG   .      .",
      inUltima: " : P&L weekly, CRM-, KPI-",
      formula: "    ",
      examples: [
        "P&L weekly:   vs ",
        "CRM-:    ",
        "HR-:       "
      ],
      link: "#control-panel"
    },
    {
      number: 4,
      icon: "Users",
      title: "  ",
      subtitle: "Cadence of Accountability",
      description: "    :         .  ,    .",
      inUltima: "  30-45        ",
      formula: "      ",
      examples: [
        " : 200 , 15 , 3 ",
        " :    2- ",
        "  :  ,  10 "
      ],
      link: "#meeting-cycle"
    }
  ];

  const bottomNote = fourDX?.note || " 4    ,   .  ULTIMA       .";

  //  
  const iconMap = {
    Target: Target,
    TrendingUp: TrendingUp,
    BarChart2: BarChart2,
    Users: Users
  };

  const getIcon = (iconName, size = 28) => {
    const IconComponent = iconMap[iconName] || Target;
    return <IconComponent size={size} />;
  };

  //    
  const handleLink = (link) => {
    if (!link) return;
    if (link.startsWith("#")) {
      const sectionId = link.slice(1);
      if (scrollToSection) {
        scrollToSection(sectionId);
      } else {
        const el = document.getElementById(sectionId);
        el?.scrollIntoView({ behavior: "smooth" });
      }
    } else {
      window.open(link, "_blank", "noopener,noreferrer");
    }
  };

  return (
    <section id={id} className="section highlight-section">
      <div className="container">
        {/* Header */}
        <div className="section-header fade-in">
          <BookOpen size={32} className="section-icon" />
          <h2>{title}</h2>
          <p className="section-subtitle">{subtitle}</p>
        </div>

        {/* Intro */}
        <div className="card fade-in" style={{ marginBottom: '2rem', textAlign: 'center' }}>
          <p style={{ fontSize: '1.1rem', lineHeight: '1.8' }}>{intro}</p>
        </div>

        {/* Disciplines */}
        <div className="fourdx-grid" style={{ 
          display: 'grid', 
          gap: '2rem',
          marginBottom: '2rem'
        }}>
          {disciplines.map((discipline, idx) => (
            <div 
              key={idx} 
              className="fourdx-card card fade-in" 
              style={{ 
                animationDelay: `${idx * 0.1}s`,
                position: 'relative',
                padding: '2rem',
                background: 'linear-gradient(135deg, rgba(99, 102, 241, 0.05) 0%, rgba(139, 92, 246, 0.05) 100%)',
                border: '1px solid rgba(99, 102, 241, 0.2)'
              }}
            >
              {/* Number Badge */}
              <div style={{
                position: 'absolute',
                top: '1rem',
                right: '1rem',
                width: '40px',
                height: '40px',
                borderRadius: '50%',
                background: 'linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%)',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                fontSize: '1.25rem',
                fontWeight: 'bold',
                color: 'white'
              }}>
                {discipline.number}
              </div>

              {/* Icon & Title */}
              <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '1rem' }}>
                <div style={{
                  width: '48px',
                  height: '48px',
                  borderRadius: '12px',
                  background: 'rgba(99, 102, 241, 0.1)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  color: '#6366f1'
                }}>
                  {getIcon(discipline.icon, 24)}
                </div>
                <div>
                  <h3 style={{ margin: 0, fontSize: '1.25rem' }}>{discipline.title}</h3>
                  <p style={{ 
                    margin: 0, 
                    fontSize: '0.9rem', 
                    color: 'rgba(255, 255, 255, 0.6)',
                    fontStyle: 'italic'
                  }}>
                    {discipline.subtitle}
                  </p>
                </div>
              </div>

              {/* Description */}
              <p style={{ marginBottom: '1rem', lineHeight: '1.7' }}>
                {discipline.description}
              </p>

              {/* In ULTIMA */}
              <div style={{ 
                background: 'rgba(99, 102, 241, 0.1)', 
                padding: '1rem', 
                borderRadius: '8px',
                marginBottom: '1rem',
                borderLeft: '3px solid #6366f1'
              }}>
                <strong style={{ color: '#6366f1' }}> ULTIMA:</strong>
                <p style={{ margin: '0.5rem 0 0 0' }}>{discipline.inUltima}</p>
              </div>

              {/* Formula */}
              {discipline.formula && (
                <div style={{ 
                  textAlign: 'center', 
                  padding: '1rem',
                  background: 'rgba(255, 255, 255, 0.03)',
                  borderRadius: '8px',
                  marginBottom: '1rem',
                  fontFamily: 'monospace',
                  fontSize: '1.1rem',
                  fontWeight: 'bold',
                  color: '#8b5cf6'
                }}>
                  {discipline.formula}
                </div>
              )}

              {/* Examples */}
              {discipline.examples && discipline.examples.length > 0 && (
                <div>
                  <strong style={{ display: 'block', marginBottom: '0.5rem' }}>:</strong>
                  <ul className="final-list dots" style={{ marginBottom: '1rem' }}>
                    {discipline.examples.map((example, i) => (
                      <li key={i}>{example}</li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Link */}
              {discipline.link && (
                <button
                  onClick={() => handleLink(discipline.link)}
                  className="cta-button secondary"
                  style={{ width: '100%', marginTop: '1rem' }}
                >
                  
                  <ArrowRight size={18} />
                </button>
              )}
            </div>
          ))}
        </div>

        {/* Bottom Note */}
        <div className="card fade-in" style={{ 
          textAlign: 'center',
          background: 'linear-gradient(135deg, rgba(16, 185, 129, 0.1) 0%, rgba(5, 150, 105, 0.1) 100%)',
          border: '1px solid rgba(16, 185, 129, 0.3)',
          padding: '2rem'
        }}>
          <p style={{ fontSize: '1.1rem', fontWeight: '500', margin: 0 }}>
            {bottomNote}
          </p>
        </div>
      </div>
    </section>
  );
}
